.grid {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 100%;
  column-gap: 10px;
}

.question-section {
  text-align: left;
  width: 90%;
  margin: 30px 0 0 30px;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
  font-size: 18px;
  text-transform: uppercase;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.button {
  width: 90%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

.button:hover {
  background-color: #555e7d;
}

.button:focus {
  outline: none;
}

.button svg {
  margin-right: 5px;
}
